import React, { useState, useEffect } from "react";
import phonebookServices from "./services/phonebook";
import Persons from "./components/Persons";
import PersonForm from "./components/PersonForm";
import Filter from "./components/Filter";
import Notification from "./components/Notification";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [personsToShow, setPersonsToShow] = useState(persons);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [notification, setNotification] = useState(null);
  const [notificationType, setNotificationType] = useState("");

  useEffect(() => {
    phonebookServices.getAll().then((data) => setPersons(data));
  }, []);

  useEffect(() => {
    setPersonsToShow(persons);
  }, [persons]);
  const handleQueryChange = (event) => {
    let query = event.target.value;

    let sortedPersons = persons.filter((person) => {
      return person.name.includes(query);
    });

    setPersonsToShow(sortedPersons);
  };

  const handleNameInputChange = (event) => {
    setNewName(event.target.value);
  };

  const handleNumberInputChange = (event) => {
    setNewNumber(event.target.value);
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();

    let personAlreadyExists = persons.find((person) => {
      return person.name === newName;
    });

    let newPerson = {
      name: newName,
      number: newNumber,
    };

    if (personAlreadyExists) {
      const confirm = window.confirm(
        `${personAlreadyExists.name} is already added to phonebook, replace the old number with a new one ?`
      );
      if (!confirm) {
        return;
      }

      newPerson = {
        ...newPerson,
        id: personAlreadyExists.id,
      };

      phonebookServices
        .updateNumber(newPerson)
        .then((updatedPerson) => {
          setPersons(
            persons.map((p) => {
              return p.id !== updatedPerson.id ? p : updatedPerson;
            })
          );
        })
        .catch((error) => {
          setPersons(
            persons.filter((p) => {
              return p.id !== newPerson.id;
            })
          );

          setNotification(
            `Information of ${newPerson.name} has already been removed from server`
          );
          setNotificationType("error");

          setTimeout(() => setNotification(null), 2000);
        });
    } else {
      phonebookServices.addPerson(newPerson).then((data) => {
        setPersons(persons.concat(data));
      });
    }

    setNotification(`Added ${newPerson.name}`);
    setNotificationType("success");
    setNewName("");
    setNewNumber("");
    setTimeout(() => setNotification(null), 3000);
  };

  const handlePersonDelete = (person) => {
    const confirm = window.confirm(`Delete ${person.name} ?`);
    if (!confirm) {
      return;
    }
    phonebookServices.deletePerson(person.id).then((status) => {
      if (status === 200) {
        setPersons(
          persons.filter((p) => {
            return p.id !== person.id;
          })
        );
      } else {
        console.log("an error occured");
      }
    });
  };

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={notification} type={notificationType} />
      <Filter handleQueryChange={handleQueryChange} />
      <h3>add a new</h3>
      <PersonForm
        handleFormSubmit={handleFormSubmit}
        newName={newName}
        handleNameInputChange={handleNameInputChange}
        newNumber={newNumber}
        handleNumberInputChange={handleNumberInputChange}
      />
      <h2>Numbers</h2>
      <Persons
        personsToShow={personsToShow}
        handlePersonDelete={handlePersonDelete}
      />
    </div>
  );
};

export default App;



