import React from "react";

const Persons = ({ personsToShow, handlePersonDelete }) => {
  return (
    <div>
      {personsToShow.map((person, i) => {
        return (
          <p key={i}>
            {person.name} {person.number}{" "}
            <button onClick={() => handlePersonDelete(person)}>delete</button>
          </p>
        );
      })}
    </div>
  );
};

export default Persons;



